import React from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import './left-image-card.css';

export interface LeftImageCardProps {
    image: any;
    title: string;
    description: any;
    link: string;
    linkText: string;
}

function ImageLeftCard({image, title, description, link, linkText}: LeftImageCardProps) {
  return (
    <Card className='h-100'>
      <Row className="g-0"> {/* Use g-0 to remove gutter between columns */}
        <Col md={5}> {/* Adjust column size for image as needed */}
          <Card.Img className='img-fluid img-thumbnail' src={image} />
        </Col>
        <Col md={7}> {/* Adjust column size for card body as needed */}
          <Card.Body>
            <Card.Title>{title}</Card.Title>
            <Card.Text>
              {description}
            </Card.Text>
            <div className='d-flex justify-content-end pt-2'>
                <Button variant="light">{linkText}</Button> 
            </div>
          </Card.Body>          
        </Col>
      </Row>
    </Card>
  );
}

export default ImageLeftCard;