import React from 'react';
import { Carousel, Image, Row, Col, Button, Container } from 'react-bootstrap';

import './right-image-carousel.css';

export interface CarouselItem {
    image: any;
    author: string;
    quote: any;
    link: string;
    linkText: string;
}

export interface RightImageCarouselProps {
    items: CarouselItem[]
}

function RightImageCarousel({items}: RightImageCarouselProps) {
  return (
    <Carousel className='col-12 col-lg-10 col-xxl-6'>
    { items.map(({image, author, quote, link, linkText}: CarouselItem, index) => (
        <Carousel.Item>
            <Row className="align-items-center"> 
                <Col md={9}>                
                    <div className='px-5 text-center'>
                        <div>{quote}</div>
                        <div className='py-4'>{author}</div>
                        <div className='d-flex justify-content-center pt-2'>
                            <Button variant="light">{linkText}</Button> 
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <Container style={{ 
                        backgroundImage: `url(${image})`,
                        backgroundRepeat: 'no-repeat',
                        backgroundSize: 'cover',
                        backgroundPosition: 'center',                        
                        height: '300px',
                        paddingRight: '0px'
                    }}></Container>
                </Col>
            </Row>
        </Carousel.Item>
    ))}
    </Carousel>
  );
}

export default RightImageCarousel;